.container {
	position: relative;
	margin: auto;
}

.img {
	width: 100%;
	height: 300px;
	object-fit: cover;
}

.mySlides {
	display: block;
}

.mySlides_hide {
	display: none;
}

.prev, .next {
	cursor: pointer;
	position: absolute;
	top: 50%;
	width: auto;
	padding: 16px;
	margin-top: -22px;
	color: rgb(172, 136, 136);
	font-weight: bold;
	font-size: 26px;
	transition: .6 ease;
	border-radius: 0 3px 3px 0;
	user-select: none;
}

.next {
	right: 0;
	border-radius: 3px 0 0 3px;
}

.prev:hover, .next:hover {
	background-color: rgba(0, 0, 0, .8);
}

.dot {
	cursor: pointer;
	height: 15px;
	width: 15px;
	margin: 0 2px;
	background-color: #bbb;
	border-radius: 50%;
	display: inline-block;
	transition: background-color .6s	ease;
}

.active, .dot:hover {
	background-color: #717171;
}

.fade {
	-webkit-animation: false;
	-webkit-animation-direction: 1.5s;
	animation-name: fade;
	animation-duration: 1.5s;
}

@-webkit-keyframes fade {
	from { opacity: .8;}
	to { opacity: 1;}
}

@keyframes fade {
	from {opacity: .8;}
	to {opacity: 1;}
}